import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import { Row, Card, Form, Input } from 'antd';
import { requiredRule } from 'utils/validateRules';
import {
  getBuildingBasesById,
  createBuildingBases,
  updateBuildingBases,
} from 'services/buildingBase';
import { getQueryParamId } from 'utils/urlUtil';

const { TextArea } = Input;

@Form.create({ name: 'formGroundworkTrainning' })
class FormGroundworkTraining extends Component {
  state = {
    buildingBaseId: false,
    initialValue: {
      id: '',
      label: '',
    },
  };

  componentDidMount = () => {
    if (getQueryParamId()) {
      this.getBuildingBasesById(getQueryParamId());
    }
  };

  getBuildingBasesById = async id => {
    this.setState({ buildingBaseId: id });
    const res = await getBuildingBasesById({ id });
    const initialValue = { ...this.state.initialValue, ...res.data };
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  saveData = async values => {
    const { buildingBaseId } = this.state;
    try {
      if (!buildingBaseId) {
        await createBuildingBases({
          label: values.label,
          address: values.address,
        });
      } else {
        await updateBuildingBases({
          id: buildingBaseId,
          label: values.label,
          address: values.address,
        });
      }
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    return (
      <Card>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit}>
            <Form.Item {...formItemLayout} label="Tên cơ sở" hasFeedback>
              {getFieldDecorator('label', {
                initialValue: initialValue.label,
                rules: [requiredRule],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Địa chỉ" hasFeedback>
              {getFieldDecorator('address', {
                initialValue: initialValue.address,
              })(<TextArea rows={4} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormGroundworkTraining {...props} />
  </Page>
));



