import { request } from 'utils';

const prefix = '/api/categories';

export const getBuildingBases = ({
  label,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
  prefix,
  url: '/BuildingBase',
  method: 'GET',
  params: {
    label,
    page,
    pageSize,
    orderBy,
    isPagingEnabled
  },
  data: {}
});


export const createBuildingBases = ({ label, address }) =>
  request({
    prefix,
    url: `/BuildingBase`,
    data: {
      label,
      address
    },
    method: 'POST',
  });

export const deleteBuildingBases = ({ id }) =>
  request({
    prefix,
    url: `/BuildingBase/${id}`,
    method: 'DELETE',
    data: { id },
  });

export const getBuildingBasesById = ({ id }) =>
  request({
    prefix,
    url: `/BuildingBase/${id}`,
    method: 'GET',
    params: { id },
  });

export const updateBuildingBases = ({ id, label, address }) =>
  request({
    prefix,
    url: `/BuildingBase/${id}`,
    data: {
      id,
      label,
      address
    },
    method: 'PUT',
  });




